.box {
  max-width: 1440px;
  margin: 96px auto;
  padding: 0 40px;
  display: flex;
  gap: 95px;
  align-items: center;
  justify-content: space-between;
}
.box .box_img {
  flex: 50% 1 1;
}
.box .box_img img {
  width: 100%;
  object-fit: contain;
}
.box .box_content {
  flex: 50% 1 1;
  color: #121212;
}
.box .box_content .box-title {
  font-weight: 700;
  font-size: 42px;
  margin-bottom: 24px;
}
.box .box_content .box-text {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-bottom: 32px;
}
.box .box_content .box-button {
  border: none;
  background-color: #E4C1AE;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 16px;
  padding: 16px 40px;
  border-radius: 32px;
}

@media (max-width: 1020px) {
  .box {
    flex-wrap: wrap;
    gap: 24px;
  }
  .box .box_img {
    flex-basis: 100%;
  }
  .box .box_content {
    flex-basis: 100%;
  }
  .box .box_content .box-title {
    margin-bottom: 12px;
  }
  .box .box_content .box-text {
    margin-bottom: 12px;
  }
}
body {
  font-family: "Roboto Flex", sans-serif;
}